.buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.buttons button {
  -webkit-flex-basis: 49%;
          flex-basis: 49%;
}
.item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-basis: 24%;
            flex-basis: 24%;
    margin: 5px 5px 5px 0;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    position: relative;
}
.item img {
    width: 100%;
    height: 100%;
}
.image-list {
    margin: 20px 0;
}
.item-info {
    position: absolute;
    bottom: 0;
    right: 10px;
}
.item-info a {
    color: #fff;
    text-decoration: underline;
}
.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
}
.card:hover {
  cursor: pointer;
}

h4 {
  font-size: 20px !important;
  text-align: center;
  margin: 20px 0 !important;
}

.color-red {
  color: red;
}
